<template>
  <el-card class="box-card">
    <div slot="header" class="clearfix">
      <span>任务</span>
      <el-button style="float: right; padding: 3px 0" type="text" @click="toClose">关闭</el-button>
    </div>
    <el-form
      :model="forms"
      :rules="rules"
      ref="editForms"
      class="edit-forms"
      label-position="left"
      :label-width="labelWidth"
      :inline="true"
    >
      <el-form-item label="商品名称" prop="goodsName">
        <el-input v-model="forms.goodsName" placeholder="标题" style="width:800px"></el-input>
      </el-form-item>
      <div></div>
      <el-form-item label="所属类目" prop="goodsCategoryId">
        <el-cascader
          :options="cateTree"
          size="small"
          @change="handleTypeChange"
          v-model="selectCateTree"
          placeholder="类目"
          clearable
        ></el-cascader>
      </el-form-item>
      <div></div>
      <el-form-item label="关键字">
        <el-input v-model="forms.goodsKeywords" placeholder="商品关键字，采用逗号间隔" style="width:400px"></el-input>
      </el-form-item>
      <div></div>
      <el-form-item label="商品图标" prop="goodsPicUrl">
        <el-upload
          class="avatar-uploader"
          action
          :show-file-list="false"
          :http-request="uploadImage"
          :data="{type:'pic'}"
        >
          <img v-if="forms.goodsPicUrl" :src="forms.goodsPicUrl" class="avatar" />
          <i v-else class="el-icon-plus avatar-uploader-icon"></i>
        </el-upload>
      </el-form-item>

      <div>
        <el-form-item label="浏览量" prop="goodsHit">
          <el-input v-model="forms.goodsHit" style="width:100px" size="small"></el-input>
        </el-form-item>
        <el-form-item label="库存" prop="goodsStock">
          <el-input v-model="forms.goodsStock" style="width:100px" size="small"></el-input>
        </el-form-item>
        <el-form-item label="已售出" prop="goodsSaleNum">
          <el-input v-model="forms.goodsSaleNum" style="width:100px" size="small"></el-input>
        </el-form-item>
      </div>

      <div>
        <el-form-item label="原价" prop="goodsOriginalPrice">
          <el-input v-model="forms.goodsOriginalPrice" style="width:100px" size="small"></el-input>
        </el-form-item>
        <el-form-item label="收购价" prop="goodsPurchasingPrice">
          <el-input v-model="forms.goodsPurchasingPrice" style="width:100px" size="small"></el-input>
        </el-form-item>
        <el-form-item label="现价" prop="goodsPrice">
          <el-input v-model="forms.goodsPrice" style="width:100px" size="small"></el-input>
        </el-form-item>
        <el-form-item label="运费" prop="goodsTransfee">
          <el-input v-model="forms.goodsTransfee" style="width:100px" size="small"></el-input>
        </el-form-item>
      </div>
      <div>
        <el-form-item label="是否新品首发" prop="goodsIsNew">
          <el-select
            v-model="forms.goodsIsOnSale"
            style="width: 100px"
            placeholder="新品首发"
            size="small"
          >
            <el-option v-for="(k,v,index) in goodsIsNewEnum" :key="index" :label="k" :value="v*1"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="是否人气推荐" prop="goodsIsHot">
          <el-select
            v-model="forms.goodsIsOnSale"
            style="width: 100px"
            placeholder="人气推荐"
            size="small"
          >
            <el-option v-for="(k,v,index) in goodsIsHotEnum" :key="index" :label="k" :value="v*1"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="状态" prop="goodsIsHot">
          <el-select
            v-model="forms.goodsIsOnSale"
            style="width: 100px"
            placeholder="状态"
            size="small"
          >
            <el-option
              v-for="(k,v,index) in goodsIsOnSaleEnum"
              :key="index"
              :label="k"
              :value="v*1"
            ></el-option>
          </el-select>
        </el-form-item>
      </div>

      <div></div>
      <el-form-item label="单位" prop="goodsUnit">
        <el-input v-model="forms.goodsUnit" style="width:100px" size="small"></el-input>
      </el-form-item>
      <el-card class="box-card">
        <div slot="header" class="clearfix">
          <span>详情</span>
        </div>
        <el-form-item label="简介">
          <el-input
            type="textarea"
            :rows="2"
            placeholder="请输入内容"
            v-model="goodsDetail.goodsBrief"
            style="width:600px"
          ></el-input>
        </el-form-item>
        <div></div>
        <el-form-item label="轮播图">
          <el-upload
            class="upload-demo"
            action
            :http-request="uploadImage"
            :data="{type:'luobo'}"
            list-type="picture"
            :file-list="luobos"
            :on-remove="handleRemove"
          >
            <el-button size="small" type="primary">点击上传</el-button>
            <div slot="tip" class="el-upload__tip">只能上传jpg/png文件，且不超过500kb</div>
          </el-upload>
        </el-form-item>
        <quill-editor
          v-model="goodsDetail.goodsDetail"
          ref="myQuillEditor"
          style="height: 500px;"
          :options="editorOption"
        ></quill-editor>
      </el-card>
    </el-form>

    <div style="display: flex;
    justify-content: center;
    width: 100%;">
      <el-button @click="toClose">取 消</el-button>
      <el-button type="primary" @click="submit">保存</el-button>
    </div>
  </el-card>
</template>

<script>
import {
  Required,
  Float2And0,
  NumberString,
  FillerFieldRules,
  validateURL,
  Float2Max100
} from "@/utils/validateRules";
import { fillerLeft, resetObject, getToken } from "@/utils/common";
import { getDateStr } from "@/utils/date";
import {
  uploadImage,
  loadCateTree,
  editGoodsAndDetail,
  loadGoodsAndDetailById
} from "@/api/goods/goodsManage";
import {
  GoodsIsOnSaleEnum,
  GoodsIsNewEnum,
  GoodsIsHotEnum
} from "@/utils/Immutable";
import { quillEditor } from "vue-quill-editor";
import "quill/dist/quill.core.css";
import "quill/dist/quill.snow.css";
import "quill/dist/quill.bubble.css";
export default {
  data() {
    return {
      forms: {
        id: "",
        goodsName: "",
        goodsCategoryId: "",
        goodsKeywords: "",
        goodsIsOnSale: 0,
        goodsSortOrder: 0,
        goodsPicUrl: "",
        goodsShareUrl: "",
        goodsIsNew: 0,
        goodsIsHot: 0,
        goodsUnit: "件",
        goodsPurchasingPrice: 0,
        goodsOriginalPrice: 0,
        goodsPrice: 0,
        goodsTransfee: 0,
        goodsSaleNum: 0,
        goodsStock: 0,
        goodsHit: 0
      },
      goodsDetail: {
        goodsDetail: "",
        goodsGallery: [],
        goodsBrief: ""
      },
      luobos: [],
      goodsIsOnSaleEnum: GoodsIsOnSaleEnum,
      goodsIsNewEnum: GoodsIsNewEnum,
      goodsIsHotEnum: GoodsIsHotEnum,
      labelWidth: "200",
      rules: {
        goodsName: [Required],
        goodsCategoryId: [Required],
        goodsPicUrl: [Required],
        goodsHit: [NumberString, Required],
        goodsStock: [NumberString, Required],
        goodsSaleNum: [NumberString, Required],
        goodsOriginalPrice: [Float2And0, Required],
        goodsPrice: [Float2And0, Required],
        goodsPurchasingPrice: [Float2And0, Required],
        goodsTransfee: [Float2And0, Required],
        goodsUnit: [Required]
      },
      headers: { Authorization: getToken() },
      editorOption: {},
      cateTree: [],
      selectCateTree: []
    };
  },
  components: {
    quillEditor
  },
  mounted() {
    this.loadCateTree();
    let id = this.$route.query.id;
    if (id) {
      this.loadGoodsById(id);
    }
  },
  methods: {
    async loadGoodsById(id) {
      let data = await loadGoodsAndDetailById({ id: id });
      if (data.code != 0) {
        this.$message.error(data.msg);
        return;
      }
      this.forms = data.data.goods;
      this.goodsDetail = data.data.detail;
      let goodsGallery = data.data.detail.goodsGallery;
      this.luobos = !goodsGallery
        ? []
        : JSON.parse(goodsGallery).map((v, i) => {
            return { name: i, url: v };
          });
      let cateId = this.forms.goodsCategoryId;
      let arr = this.getParentId(cateId);
      this.selectCateTree = arr;
    },
    getParentId(id) {
      for (let i = 0; i < this.cateTree.length; i++) {
        const element = this.cateTree[i];

        if (element.value == id) {
          return [id];
        }
        if (!element.children) continue;
        for (let j = 0; j < element.children.length; j++) {
          const element2 = element.children[j];
          if (element2.value == id) {
            return [element.value, element2.value];
          }
          if (!element2.children) continue;
          for (let k = 0; k < element2.children.length; k++) {
            const element3 = element2.children[k];
            if (element3.value == id) {
              return [element.value, element2.value, element3.value];
            }
          }
        }
      }
    },
    async loadCateTree() {
      const data = await loadCateTree();
      if (data.code != 0) {
        this.$message.error(data.msg);
      }
      this.cateTree = data.data;
    },
    handleRemove(file, fileList) {
      this.luobos = this.luobos.filter(v => v.uid != file.uid);
    },
    handleTypeChange(v) {
      if (!v) {
        this.forms.goodsCategoryId = "";
        return;
      }
      this.forms.goodsCategoryId = v[v.length - 1];
    },
    submit() {
      let _this = this;
      this.$refs.editForms.validate(valid => {
        if (!valid) {
          this.$message.error("请按照提示正确填写内容！");
          return false;
        }
        _this.save();
      });
    },
    async save() {
      if (!this.monualValide()) return false;
      this.goodsDetail.goodsGallery =
        this.luobos.length == 0
          ? ""
          : JSON.stringify(this.luobos.map(v => v.url));
      let params = {
        goods: this.forms,
        detail: this.goodsDetail
      };
      let data = await editGoodsAndDetail(params);
      if (data.code != 0) {
        this.$message.error(data.msg);
        return;
      }
      this.toClose();
    },
    toClose() {
      this.$router.push({ name: "goodslist" });
    },
    monualValide() {
      if (!validateURL(this.forms.goodsPicUrl)) {
        this.$message.error("商品图片没有上传");
        return false;
      }

      return true;
    },

    openDialog() {
      resetObject(this.forms);
      this.$set(this.forms, "id", null);
      this.$refs.editForms.resetFields();
    },
    FillerFormField(id, data) {
      //可以外部填充回写做编辑用，也可以请求详情接口填充表单
      this.$set(this.forms, "id", id);
      fillerLeft(this.forms, data);
    },
    async uploadStepImage(fileObj) {
      const data = await this.upload(fileObj);
      const index = fileObj.data.index;
      this.addList[index].taskStepPicUrl = data;
    },
    //任务图标文件上传成功后
    async uploadImage(fileObj) {
      let picUrl = await this.upload(fileObj);
      if (fileObj.data.type == "pic") this.forms.goodsPicUrl = picUrl;
      else {
        this.luobos.push({ uid: fileObj.file.uid, url: picUrl });
      }
    },
    removeImage(item) {
      item.taskStepPicUrl = "";
    },
    async upload(fileObj) {
      console.log(fileObj);
      if (!this.beforeAvatarUpload(fileObj.file)) return;
      let formData = new FormData();
      formData.set("file", fileObj.file);
      const data = await uploadImage(formData);
      if (data.code != 0) {
        this.$message.error(data.msg);
        return "";
      }
      return data.data;
    },
    //文件上传前
    beforeAvatarUpload(file) {
      const isJPG = file.type === "image/jpeg" || file.type === "image/png";
      const isLt2M = file.size / 1024 / 1024 < 2;
      if (!isJPG) {
        this.$message.error("上传头像图片只能是 JPG 格式!");
      }
      if (!isLt2M) {
        this.$message.error("上传头像图片大小不能超过 2MB!");
      }
      return isJPG && isLt2M;
    }
  }
};
</script>
<style lang="scss">
.avatar-uploader .el-upload {
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}
.avatar-uploader .el-upload:hover {
  border-color: #409eff;
}
.avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 60px;
  height: 60px;
  line-height: 60px;
  text-align: center;
}
.avatar {
  width: 80px;
  height: 80px;
  display: block;
}
.my-icon {
  font-size: 20px;
  position: absolute;
  left: 60px;
  top: 0px;
  color: red;
}
</style>